var vm = {
  name: "player",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    src: {
      type: String,
      default: ""
    }
  },
  data() {
    vm = this;
    return {
		autoplay: true,
		videoCtx:''
	};
  },
  created() {
	// console.log('created')
	
  },
  mounted() {
	
  },
  methods: {
    close() {
		setTimeout(()=>{
			vm.$emit("close", false);
		},300)
      
    }
  }
};
export default vm;
